(function () {
    'use strict';
    var angular = require('angular');
    require('./personalDetailsAcknowledgement.component.scss');

    var personalDetailsAcknowledgement = angular.module('app').component('personalDetailsAcknowledgement', {
        template: require('./personalDetailsAcknowledgement.component.html'),
        controller: ['$anchorScroll', 'appState', 'analyticService', function ($anchorScroll, appState, analyticService) {
            var vm = this;

            vm.$onInit = function () {
                vm.isStaffJourney = appState.isStaffJourney();
                analyticService.trackPageLoad(analyticService.pageList.personalDetailsAcknowledgement);
                appState.clearAll();
                $anchorScroll();
            };

            vm.exit = function () {
                analyticService.buttonList.buttonClick.event_content = "Personal Details Acknowledgement: Exit"
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                window.location.href = "http://www.hsbc.co.in/";
            }
        }]
    });

    module.exports = personalDetailsAcknowledgement.name;
})();



